import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"


const vitaminaE = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina E"
                meta="Toda la información que necesitas sobre la vitamina E. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina E?"
                description="Toda la información que necesitas sobre la vitamina E. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina E?"
                lang="es"
            />
            <h2>Vitamina E</h2>
            <p>La vitamina E es una vitamina que se disuelve en la grasa. Se encuentra en muchos alimentos como los
                aceites vegetales, los cereales, la carne, las aves, los huevos, las frutas, las verduras y el aceite de
                germen de trigo. Tambi&eacute;n est&aacute; disponible como suplemento.</p>
            <p></p>
            <h2>Vitamina E para que sirve</h2>
            <p>La vitamina E se utiliza para el tratamiento de la deficiencia de vitamina E, que es poco frecuente, pero
                puede ocurrir en personas con ciertos trastornos gen&eacute;ticos y en beb&eacute;s prematuros de muy
                bajo peso. La vitamina E tambi&eacute;n se utiliza para muchas otras afecciones, pero no existen pruebas
                cient&iacute;ficas s&oacute;lidas que respalden estos otros usos.</p>
            <p></p>
            <p>La Asociaci&oacute;n Americana del Coraz&oacute;n recomienda obtener antioxidantes, incluida la vitamina
                E, mediante una dieta bien equilibrada con alto contenido de frutas, verduras y granos enteros, en lugar
                de tomar suplementos, hasta que se conozcan m&aacute;s los riesgos y beneficios de tomar
                suplementos.</p>
            <p></p>
            <p>Es un antioxidante. Esto significa que protege el tejido corporal del da&ntilde;o causado por las
                sustancias llamadas radicales libres. Los radicales libres pueden da&ntilde;ar las c&eacute;lulas, los
                tejidos y los &oacute;rganos. Se cree que juegan un papel en ciertas condiciones relacionadas con el
                envejecimiento.</p>
            <p>El cuerpo tambi&eacute;n necesita vitamina E para ayudar a mantener el sistema inmunol&oacute;gico fuerte
                contra los virus y las bacterias. La vitamina E tambi&eacute;n es importante en la formaci&oacute;n de
                los gl&oacute;bulos rojos. Ayuda al cuerpo a utilizar la vitamina K. Tambi&eacute;n ayuda a ensanchar
                los vasos sangu&iacute;neos y a evitar que la sangre se coagule en su interior.</p>
            <p>Las c&eacute;lulas utilizan la vitamina E para interactuar entre s&iacute;. Les ayuda a realizar muchas
                funciones importantes.</p>
            <p>A&uacute;n es necesario investigar m&aacute;s a fondo si la vitamina E puede prevenir el c&aacute;ncer,
                las enfermedades card&iacute;acas, la demencia, las enfermedades hep&aacute;ticas y los accidentes
                cerebrovasculares.</p>
            <p></p>
            <p></p>
            <p></p>
            <h2>vitamina e alimentos</h2>
            <p></p>
            <p>La vitamina E se encuentra en los aceites vegetales, los frutos secos, las semillas, las frutas y las
                verduras.</p>
            <p></p>
            <ul>
                <li>Aceite de germen de trigo</li>
                <li>Aceite de girasol, c&aacute;rtamo y soja</li>
                <li>Semillas de girasol</li>
                <li>Almendras</li>
                <li>Cacahuetes, mantequilla de cacahuete</li>
                <li>Remolacha, berza, espinacas</li>
                <li>Calabaza</li>
                <li>Pimiento rojo</li>
                <li>Esp&aacute;rragos</li>
                <li>Mango</li>
                <li>Aguacate</li>
            </ul>
            <p></p>
            <h2>Deficiencia de vitamina E</h2>
            <p>Debido a que la vitamina E se encuentra en una variedad de alimentos y suplementos, una deficiencia de
                esta vitamina es poco frecuente. Las personas que tienen trastornos digestivos o no absorben las grasas
                adecuadamente (por ejemplo, pancreatitis, fibrosis qu&iacute;stica, enfermedad cel&iacute;aca) pueden
                desarrollar una deficiencia de vitamina E. Los siguientes son signos comunes de una deficiencia:</p>
            <p></p>

            <p><b>Retinopat&iacute;a</b> (da&ntilde;o en la retina de los ojos que puede perjudicar la visi&oacute;n)</p>
            <p><b>Neuropat&iacute;a perif&eacute;rica</b> (da&ntilde;o a los nervios perif&eacute;ricos, generalmente en las
                manos o los pies, causando debilidad o dolor)</p>
            <p><b>Ataxia</b> (p&eacute;rdida de control de los movimientos del cuerpo)</p>
            <p><b>Disminuci&oacute;n de la funci&oacute;n inmunol&oacute;gica</b></p>
            <p></p>
            <h2>Efectos secundarios, toxicidad de la vitamina E</h2>
            <p>La vitamina E es una vitamina relativamente segura. La vitamina E es soluble en grasa y puede acumularse en los tejidos del cuerpo. Pero esto es raro. Los s&iacute;ntomas de un exceso de vitamina E incluyen:</p>
            <p></p>
            <ul>
                <li>Fatiga</li>
                <p></p>
                <li>Debilidad</li>
                <p></p>
                <li>N&aacute;useas</li>
                <p></p>
                <li>Visi&oacute;n borrosa</li>
                <p></p>
                <li>Gas</li>
                <p></p>
                <li>Diarrea</li>
            </ul>
            <p></p>
            <p>La vitamina E puede aumentar los efectos de los medicamentos anticoagulantes orales. Esto puede causar un aumento en el tiempo de coagulaci&oacute;n y en el sangrado.</p>
            <p></p>
            <p>Grandes dosis de vitamina E pueden disminuir las reservas de vitamina A del cuerpo.</p>
            <div style={{height: `300px`}}>
            </div>
        </Layout>
    )
}

export default vitaminaE
